@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@100;200&family=Open+Sans:wght@300&display=swap);
*,
*::before,
*::after {
  box-sizing: border-box;
  /* border: var(--dev-border); */
}

:root {
  --ff-primary: "Open Sans", sans-serif;
  --ff-secondary: "Josefin Sans", sans-serif;

  --fs-h0: 5rem;
  --fs-h1: 3rem;
  --fs-h2: 2.15rem;
  --fs-h3: 1.8rem;
  --fs-h4: 1.35rem;
  --fs-body: 1rem;

  --clr-black: #18181c;
  --clr-dark: #1e2125;
  --clr-light: #f8f8f8;
  --clr-light-grey: #cdcdcd;
  --clr-text: #acacac;
  --clr-dark-grey: #585858;
  --clr-light-blue: #32c5ff;
  --clr-blue: #00adef;
  --clr-dark-blue: #0099cc;

  --dev-border: 1px solid red;
}

@media (min-width: 800px) {
  :root {
    --fs-h0: 6rem;
    --fs-h1: 4.5rem;
    --fs-h2: 3.25rem;
    --fs-h3: 2rem;
    --fs-h4: 1.5rem;
    --fs-body: 1.25rem;
  }
}

@media (min-width: 1200px) {
  :root {
    --fs-h0: 6.25rem;
    --fs-h1: 5.2rem;
    --fs-h2: 3.65rem;
    --fs-h3: 2.25rem;
    --fs-h4: 1.8rem;
    --fs-body: 1.4rem;
  }
}

@media (min-width: 1500px) {
  :root {
    --fs-h0: 7.5rem;
    --fs-h1: 5.5rem;
    --fs-h2: 3.8rem;
    --fs-h3: 2.5rem;
    --fs-h4: 2.15rem;
    --fs-body: 1.5rem;
  }
}

@media (min-width: 1750px) {
  :root {
    --fs-h0: 8.75rem;
    --fs-h1: 5.75rem;
    --fs-h2: 4rem;
    --fs-h3: 2.75rem;
    --fs-h4: 2.3rem;
    --fs-body: 1.75rem;
  }
}

@media (min-width: 2000px) {
  :root {
    --fs-h0: 10rem;
    --fs-h1: 6.1rem;
    --fs-h2: 4.1rem;
    --fs-h3: 3rem;
    --fs-h4: 2.5rem;
    --fs-body: 1.9rem;
  }
}

html {
  overflow-x: hidden;
  -ms-scroll-chaining: none;
      overscroll-behavior: none;
  background-color: #18181c;
  background-color: var(--clr-black);
}

body {
  margin: 0;
  font-family: "Open Sans", sans-serif;
  font-family: var(--ff-primary);
  font-weight: 300;
  color: #acacac;
  color: var(--clr-text);
}

a {
  text-decoration: none;
  color: unset;
}

@-webkit-keyframes appear {
  from {
    opacity: 0;
    transform: translateY(16px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes appear {
  from {
    opacity: 0;
    transform: translateY(16px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@-webkit-keyframes drop-in {
  from {
    opacity: 0;
    transform: translateY(-16px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes drop-in {
  from {
    opacity: 0;
    transform: translateY(-16px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@-webkit-keyframes grow {
  from {
    width: 0%;
  }
  to {
    width: 65%;
  }
}

@keyframes grow {
  from {
    width: 0%;
  }
  to {
    width: 65%;
  }
}

@-webkit-keyframes grow-x {
  from {
    width: 0%;
  }
  to {
    width: 90%;
  }
}

@keyframes grow-x {
  from {
    width: 0%;
  }
  to {
    width: 90%;
  }
}

@-webkit-keyframes rotate {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(359deg);
  }
}

@keyframes rotate {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(359deg);
  }
}

.intro {
  min-height: 500px;
  background-color: var(--clr-dark);
}

.intro-flex {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.intro-text {
  padding: 2rem;
  min-height: 100px;
}

hr.underline {
  border: 1px solid var(--clr-dark-grey);
  border-radius: 2px;
  margin: 0;
  -webkit-animation: 2s grow-x ease forwards;
          animation: 2s grow-x ease forwards;
}

h2.feature {
  margin: 0;
  font-size: var(--fs-h1);
  color: var(--clr-blue);
  line-height: 1.6;
  opacity: 0;
  -webkit-animation: 1s drop-in ease forwards;
          animation: 1s drop-in ease forwards;
  font-weight: 400;
}

p.description {
  color: var(--clr-light);
  font-size: calc(var(--fs-body) + 0.25rem);
  max-width: 90%;
  line-height: 1.6;
  opacity: 0;
  -webkit-animation: 1s 0.5s appear ease-out forwards;
          animation: 1s 0.5s appear ease-out forwards;
}

div.intro-image-container {
  display: grid;
  place-items: center;
  opacity: 0;
  -webkit-animation: 1s 1s appear ease-in forwards;
          animation: 1s 1s appear ease-in forwards;
}

img.intro-image {
  max-width: 80%;
  pointer-events: none;
}

.spin {
  -webkit-animation: 10s rotate linear infinite;
          animation: 10s rotate linear infinite;
}

@media (min-width: 800px) {
  img.intro-image {
    max-width: 650px;
  }
}

@media (min-width: 1000px) {
  .intro-flex {
    flex-direction: column-reverse;
  }

  div.intro-image-container {
    position: relative;
  }

  img.intro-image {
    margin-right: calc(-100% + 800px);
    max-width: 600px;
  }

  div.intro-text {
    padding: 0 2rem 2rem;
    margin-top: -8rem;
    z-index: 10;
  }

  div.intro-text.close {
    margin-top: -6rem;
  }
}

@media (min-width: 1200px) {
  .intro-flex {
    flex-direction: row;
  }

  img.intro-image {
    margin-right: 0;
    margin-right: initial;
  }

  div.intro-text {
    padding: 2rem;
    margin-top: 0;
    display: flex;
    flex-direction: column;
    max-width: 650px;
  }

  div.intro-text.close {
    margin-top: 0;
  }
}

@media (min-width: 1500px) {
  div.intro-text {
    padding: 4rem;
    max-width: 850px;
  }
}

section.section {
  min-height: 500px;
  transform: translateY(32px);
  opacity: 0;
  visibility: hidden;
  transition: 0.5s opacity ease-out, 0.5s transform ease-out;
}

section.visible {
  transform: none;
  opacity: 1;
  visibility: visible;
}

main.container {
  max-width: 80%;
  min-height: 600px;
  margin: 0 auto;
  position: relative;
  display: flex;
  flex-direction: column;
  place-items: center;
  justify-content: space-around;
}

div.image-container,
div.text-container {
  margin: 0;
  padding: 0;
  max-height: 400px;
  word-wrap: break-word;
}

div.text-container {
  max-width: 85%;
  min-width: 85%;
}

div.image-container {
  color: white;
  min-height: 300px;
  max-width: 70%;
  min-width: 70%;
}

h3.text-title {
  color: var(--clr-light-grey);
  font-size: var(--fs-h4);
  margin: 1rem 0;
  font-weight: 400;
}

p.text {
  color: var(--clr-text);
  line-height: 1.6;
  letter-spacing: 1px;
  margin: 0;
}

@media (min-width: 800px) {
  p.text {
    font-size: var(--fs-body);
  }

  main.container {
    min-height: 700px;
  }

  div.image-container {
    min-height: 400px;
  }
}

@media (min-width: 1000px) {
  section {
    min-height: 500px;
  }

  main.container {
    flex-direction: row;
    min-height: 500px;
  }

  div.image-container,
  div.text-container {
    max-width: 47.5%;
    min-width: 47.5%;
    height: 600px;
  }

  section.flip main.container {
    flex-direction: row-reverse;
  }
}

@media (min-width: 1200px) {
  section.section {
    min-height: 700px;
  }

  p.text {
    font-size: calc(var(--fs-body) + 0.1rem);
  }

  main.container {
    max-width: 75%;
    min-height: 600px;
    justify-content: space-around;
  }

  div.image-container,
  div.text-container {
    max-width: 45%;
    min-width: 45%;
    height: 600px;
  }
}

.table-row {
  align-items: center;
  grid-template-columns: repeat(2, 1fr);
  border-bottom: 1px solid var(--clr-light-grey);
}

.table-row:last-child {
  border-bottom: none;
}

.command {
  color: var(--clr-blue);
  font-size: var(--fs-h4);
  font-weight: 300;
}

.command-description {
  color: var(--clr-light-grey);
  font-size: var(--fs-body);
}

@media (min-width: 800px) {
  .table-row {
    display: grid;
  }
}

.table {
  max-width: 90%;
  margin: 0 auto;
  padding-bottom: 5rem;
}

.table-title {
  font-size: var(--fs-h3);
  margin: 1rem 0;
  color: var(--clr-light);
  font-weight: 400;
}

@media (min-width: 1000px) {
  .table {
    padding-bottom: 10rem;
  }
}

div.wave-container,
div.wave-container-flip {
  background-color: var(--clr-dark);
  padding: 0;
  width: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

svg.wave {
  width: 100%;
}

div.wave-container-flip {
  transform: rotate(180deg);
}

.footer-distributed {
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.12);
  box-sizing: border-box;
  width: 100%;
  text-align: left;
  font: bold 16px sans-serif;
  font-weight: 300;
  padding: 55px 50px;
  bottom: 0;
  background-color: var(--clr-dark);
  font-size: var(--fs-body);
  margin-top: -4px;
}

.footer-distributed .footer-left,
.footer-distributed .footer-center,
.footer-distributed .footer-right {
  display: inline-block;
  vertical-align: top;
}

.footer-distributed .footer-left {
  width: 45%;
}

.footer-distributed h2 {
  color: var(--clr-light);
  margin: 0;
  font-size: var(--fs-h3);
}

.footer-distributed .footer-links {
  color: var(--clr-blue);
  margin: 20px 0 12px;
  padding: 0;
}

.footer-distributed .footer-links * {
  display: inline-block;
  line-height: 1.8;
  text-decoration: none;
  color: inherit;
  padding: 0.2rem;
  transition: 0.2s color ease;
  cursor: pointer;
}

.footer-distributed .footer-links *:hover {
  color: var(--clr-light-blue);
}

.footer-distributed .footer-company-name {
  color: var(--clr-text);
  font-size: 14px;
  font-weight: normal;
  margin: 0;
}

.footer-distributed .footer-center {
  width: 30%;
}

.footer-distributed .footer-center p {
  display: inline-block;
  color: var(--clr-light);
  vertical-align: middle;
  margin: 0;
}

.footer-distributed .footer-center p a {
  color: var(--clr-blue);
  text-decoration: none;
  font-size: var(--fs-h4);
  transition: 0.2s color ease;
}

.footer-distributed .footer-center p a:hover {
  color: var(--clr-light-blue);
}

.footer-distributed .footer-right {
  width: 25%;
}

.footer-distributed .footer-company-about {
  line-height: 1.4;
  color: var(--clr-text);
  font-size: calc(var(--fs-body) - 0.25rem);
  font-weight: normal;
  margin: 0;
}

.footer-distributed .footer-company-about span {
  display: block;
  color: var(--clr-light);
  font-size: calc(var(--fs-h4) - 0.25rem);
  font-weight: bold;
  margin-bottom: 20px;
}

@media (max-width: 950px) {
  .footer-distributed {
    font: bold 14px sans-serif;
  }

  .footer-distributed .footer-left,
  .footer-distributed .footer-center,
  .footer-distributed .footer-right {
    display: block;
    width: 100%;
    margin-bottom: 40px;
    text-align: center;
  }

  .footer-distributed .footer-company-about {
    max-width: 50%;
    margin: 0 auto;
    font-size: var(--fs-body);
  }

  .footer-distributed .footer-company-about span {
    font-size: var(--fs-h4);
  }
  .main {
    line-height: normal;
    font-size: auto;
  }
}

.header {
  background: linear-gradient(to bottom, var(--clr-black), var(--clr-dark));
}

.logo {
  font-size: var(--fs-h2);
  text-align: center;
  margin: 0;
  font-weight: 300;
  color: var(--clr-light);
  padding: 8px 16px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  display: flex;
  place-items: center;
  justify-content: space-around;
  width: 100vw;
  cursor: pointer;
}

.logo::before,
.logo::after {
  display: none;
}

.navlink:hover {
  color: var(--clr-light);
}

.nav {
  display: flex;
  flex-direction: column;
  place-items: center;
}

.navlinks {
  display: flex;
  min-width: 100%;
  padding: 0.25rem 0 0.25rem 1rem;
  justify-content: space-around;
}

.navlink {
  color: var(--clr-light-grey);
  text-decoration: none;
  font-size: calc(var(--fs-body) - 0.1rem);
  transition: 0.2s color ease;
  font-size: var(--fs-body);
}

@media (min-width: 500px) {
  .logo::before,
  .logo::after {
    display: inline;
    display: initial;
    content: "";
    background-color: var(--clr-light-grey);
    height: 3px;
    width: 20%;
    margin: 8px;
  }
}

@media (min-width: 650px) {
  .logo {
    font-size: var(--fs-h1);
  }

  .logo::before,
  .logo::after {
    height: 4px;
    width: 25%;
  }

  .navlink {
    font-size: var(--fs-h4);
  }
}

@media (min-width: 800px) {
  .nav {
    display: flex;
    flex-direction: row;
    padding: 8px;
  }

  .logo {
    font-size: var(--fs-h3);
    width: auto;
  }

  .logo::before,
  .logo::after {
    display: none;
  }

  .navlinks {
    display: flex;
    min-width: 400px;
  }

  .navlink-container {
    padding: 8px 16px;
  }

  .navlink {
    font-size: var(--fs-body);
  }
}

@media (min-width: 1500px) {
  .navlinks {
    padding: 0.25rem 0 0.25rem 3rem;
  }
}

section.landing {
  display: grid;
  place-items: center;
  min-height: 550px;
  background-color: var(--clr-dark);
}

h2.title {
  color: var(--clr-light);
  font-weight: 300;
  font-size: calc(var(--fs-h1) - 0.5rem);
  letter-spacing: 2px;
  text-align: center;
  margin: 32px auto;
  opacity: 0;
  -webkit-animation: 1s appear forwards ease;
          animation: 1s appear forwards ease;
  max-width: 90vw;
}

p.subtitle {
  text-align: center;
  opacity: 0;
  -webkit-animation: 1s 0.2s appear forwards ease;
          animation: 1s 0.2s appear forwards ease;
  font-size: var(--fs-body);
}

hr.line {
  max-width: 65%;
  margin: 0 auto;
  background-color: var(--clr-blue);
  color: var(--clr-blue);
  border: 1px solid var(--clr-dark-grey);
  -webkit-animation: 2s grow forwards ease;
          animation: 2s grow forwards ease;
}

div.buttons {
  display: flex;
  justify-content: space-evenly;
  width: 100%;
}

.landinglink {
  flex: 1 1;
  text-align: center;
  max-width: 200px;
  text-decoration: none;
  color: var(--clr-blue);
  font-size: var(--fs-h4);
  opacity: 0;
  -webkit-animation: 1s drop-in forwards ease;
          animation: 1s drop-in forwards ease;
  -webkit-animation-delay: 0.5s;
          animation-delay: 0.5s;
  transition: 0.2s color ease;
}

.landinglink:hover {
  color: var(--clr-light-blue);
}

@media (min-width: 500px) {
  h2.title {
    font-size: var(--fs-h1);
  }

  .landinglink {
    font-size: var(--fs-h3);
  }
}

@media (min-width: 800px) {
  section.landing {
    min-height: 650px;
  }
  hr.line {
    max-width: 55%;
  }
}

main.not-found {
  min-height: 700px;
  background-color: var(--clr-dark);
  color: var(--clr-light);
  display: grid;
  place-items: center;
}

.error {
  margin: 0;
  text-align: center;
  font-weight: 300;
  font-size: calc(var(--fs-h0) * 2);
}

.message {
  margin: 0;
  text-align: center;
  font-weight: 300;
  font-size: var(--fs-h2);
}

.go-home {
  color: var(--clr-blue);
  text-align: center;
  font-size: var(--fs-h4);
  transition: 0.2s all ease;
}

.go-home:hover {
  color: var(--clr-light-blue);
}

@media (min-width: 800px) {
  .message {
    font-size: calc(var(--fs-h2) - 0.5rem);
  }
}

@media (min-width: 1200px) {
  main.not-found {
    min-height: 800px;
  }
}

