@import url("https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@100;200&family=Open+Sans:wght@300&display=swap");

*,
*::before,
*::after {
  box-sizing: border-box;
  /* border: var(--dev-border); */
}

:root {
  --ff-primary: "Open Sans", sans-serif;
  --ff-secondary: "Josefin Sans", sans-serif;

  --fs-h0: 5rem;
  --fs-h1: 3rem;
  --fs-h2: 2.15rem;
  --fs-h3: 1.8rem;
  --fs-h4: 1.35rem;
  --fs-body: 1rem;

  --clr-black: #18181c;
  --clr-dark: #1e2125;
  --clr-light: #f8f8f8;
  --clr-light-grey: #cdcdcd;
  --clr-text: #acacac;
  --clr-dark-grey: #585858;
  --clr-light-blue: #32c5ff;
  --clr-blue: #00adef;
  --clr-dark-blue: #0099cc;

  --dev-border: 1px solid red;
}

@media (min-width: 800px) {
  :root {
    --fs-h0: 6rem;
    --fs-h1: 4.5rem;
    --fs-h2: 3.25rem;
    --fs-h3: 2rem;
    --fs-h4: 1.5rem;
    --fs-body: 1.25rem;
  }
}

@media (min-width: 1200px) {
  :root {
    --fs-h0: 6.25rem;
    --fs-h1: 5.2rem;
    --fs-h2: 3.65rem;
    --fs-h3: 2.25rem;
    --fs-h4: 1.8rem;
    --fs-body: 1.4rem;
  }
}

@media (min-width: 1500px) {
  :root {
    --fs-h0: 7.5rem;
    --fs-h1: 5.5rem;
    --fs-h2: 3.8rem;
    --fs-h3: 2.5rem;
    --fs-h4: 2.15rem;
    --fs-body: 1.5rem;
  }
}

@media (min-width: 1750px) {
  :root {
    --fs-h0: 8.75rem;
    --fs-h1: 5.75rem;
    --fs-h2: 4rem;
    --fs-h3: 2.75rem;
    --fs-h4: 2.3rem;
    --fs-body: 1.75rem;
  }
}

@media (min-width: 2000px) {
  :root {
    --fs-h0: 10rem;
    --fs-h1: 6.1rem;
    --fs-h2: 4.1rem;
    --fs-h3: 3rem;
    --fs-h4: 2.5rem;
    --fs-body: 1.9rem;
  }
}

html {
  overflow-x: hidden;
  overscroll-behavior: none;
  background-color: var(--clr-black);
}

body {
  margin: 0;
  font-family: var(--ff-primary);
  font-weight: 300;
  color: var(--clr-text);
}

a {
  text-decoration: none;
  color: unset;
}

@keyframes appear {
  from {
    opacity: 0;
    transform: translateY(16px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes drop-in {
  from {
    opacity: 0;
    transform: translateY(-16px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes grow {
  from {
    width: 0%;
  }
  to {
    width: 65%;
  }
}

@keyframes grow-x {
  from {
    width: 0%;
  }
  to {
    width: 90%;
  }
}

@keyframes rotate {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(359deg);
  }
}
